.parallax-image {
	background: url(/static/media/library-img.5cfe8ee0.jpg) no-repeat center center/
		cover;
	background-attachment: fixed;
	height: 60vh;
	width: 100%;
	position: relative;
	background-size:cover;
    padding-top:1%;
    padding-bottom:1%;
}

.parallax-image::before {
	position: absolute;
	content: "";
	left: 0;
	right: 0;
	bottom: 0;
	width: 100%;
	height: 100%;
	z-index: 2;
}

.parallax-image > * {
	position: relative;
	z-index: 3;
}

.hero-content {
	color: white;
	height: 100%;
	padding: 6%;

}

.hero-content h2 {
	line-height: 1.1;
	font-size: 35px;
}

.hero-cta {
	width: -webkit-max-content;
	width: max-content;
	transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
	-webkit-transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
	-moz-transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
	-ms-transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
	-o-transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

.hero-cta:hover {
	transform: translateY(-5px);
	-webkit-transform: translateY(-5px);
	-moz-transform: translateY(-5px);
	-ms-transform: translateY(-5px);
	-o-transform: translateY(-5px);
}

.hero-cta .icon {
	transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
	-webkit-transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
	-moz-transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
	-ms-transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
	-o-transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

.hero-cta:hover .icon {
	transform: translateX(5px);
	-webkit-transform: translateX(5px);
	-moz-transform: translateX(5px);
	-ms-transform: translateX(5px);
	-o-transform: translateX(5px);
}

.hero-head {
	font-weight: 800;
	color: white;
	font-size: 30px;
	margin-left: 8%;
}

.btn {
	font-size: 2rem;
    display: inline-block;
    vertical-align: middle;
    width: -webkit-max-content;
    width: max-content;
    min-width: 80px;
    height: -webkit-max-content;
    height: max-content;
    border: 2px solid var(--text-color);
    border-radius: 5%;
    -webkit-border-radius: 5%;
    -moz-border-radius: 5%;
    -ms-border-radius: 5%;
    -o-border-radius: 5%;
	font-weight: 500;
    text-align: center;
    cursor: pointer;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    grid-gap: 15px;
    gap: 15px;
	background-color: #ee0063;
    border: 2px solid #ee0063;
	margin-left: 60%;
	color: rgb(255, 255, 255);
	padding-left: 10px;
	line-height: 1;
	border-radius: 2%;
}

.btn:hover {
	background-color: #c90054;
}

@media screen and (max-width: 640px) {
	.hero-content {
		padding: var(--space-1);
	}
}

@media screen and (max-width: 300px) {
	.hero-content h2 {
		font-size: 30px;
	}
}

